import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl, FormArray, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { PATTERNS } from 'app/shared/utils/validation-patterns';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { EVChargingSolutionService } from 'app/shared/services/ev-charging-solution/ev-charging-solution.service';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})


export class CreateComponent implements OnInit {

  form: FormGroup;
  localStorage: any;
  status = false;
  @ViewChild("activeElement") active: any;
  

  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, private router: Router,private evChargingSolService: EVChargingSolutionService,) {

  }

  ngOnInit() {
    this.initialize(); 
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      name: ['',[Validators.required]],
      role: ['',  [Validators.required]],
      offering: ['', [ Validators.required]],
      project: ['', [Validators.required]],
      remarks: ['', [Validators.required]],  
      status: this.status == true ? 'Y' : 'N'
    });

  }

  onSubmit() {
    if (this.form.valid) {
      let formValues = this.form.value;
      
      let params = {
        
        name:formValues.name,
        role:formValues.role,
        offerings:formValues.offering,
        projects:formValues.project,
        remarks:formValues.remarks,
        status: this.status == true ? 'Y' : 'N',
        created_by: this.localStorage.userId,
      }

      this.evChargingSolService.createEVChargingProvider(params).subscribe(
        (resp:any) => {
          if(resp.status == true){
            //console.log(params,resp.data);
            this.snackBar.open(MessageConstant.evChargingSolutionCreate, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/miscellaneous/evChargingSolution/crud/list/']);
          }else{
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }

        },(err) =>{
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
         }
      );
    }
    else {
      this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 5000 });
    }

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  updateStatus(event) {
    this.status = event.checked;
    this.active.checked = this.status;
  }




}


