import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MenuMasterService } from 'app/shared/services/menu-master/menu-master.service';
import { EVChargingSolutionService } from 'app/shared/services/ev-charging-solution/ev-charging-solution.service';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}


@Component({
  selector: 'app-action-dialog',
  templateUrl: './action-dialog.component.html',
  styleUrls: ['./action-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})


export class ActionDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  nav_level: any;
  project: any;
  menuType: any;
  menuIcon: any;
  parentId: any;
  status = false;
  filteredOptionsNavLevel: Observable<any[]>;
  filteredOptionsProject: Observable<any[]>;
  filteredOptionsMenuType: Observable<any[]>;
  filteredOptionsMenuIcon: Observable<any[]>;
  filteredOptionsParentId: Observable<any[]>;

  @ViewChild("activeElement") active: any;

  // messageConstant = MessageConstant;
  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, public dialogRef: MatDialogRef<ActionDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private router: Router, private menuMasterService: MenuMasterService,
    private evChargingSolService: EVChargingSolutionService) {

  }

  ngOnInit() {
    this.initialize();
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.params = this.data;
    //console.log(this.params)
    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      name: ['', [Validators.required ]],
      role: ['', [Validators.required ]],
      offering: ['', [Validators.required ]],
      project: ['', [Validators.required]],
      remarks: ['', ],
      status: this.status == true ? 'Y' : 'N'
    });

    if (this.isEditMode) {
      this.assignValues(this.params);
    }

  }

  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  onYesClick() {
    if (this.form.valid) {
      let formValues = this.form.value;

      const params: any = {
        name: formValues.name,
        role: formValues.role,
        offerings: formValues.offering,
        projects: formValues.project,
        remarks: formValues.remarks,
        status: this.status == true ? 'Y' : 'N'
      }

      if (this.isEditMode) {
        params.id = this.params.data.id,
          params.modifyby = this.localStorage.userId;
      } else {
        params.created_by = this.localStorage.userId;
      }

      if (this.isEditMode) {
        this.evChargingSolService.updateEVChargingProvider(params).subscribe(
          (resp: any) => {
            if (resp.status == true) {
              this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
              this.dialogRef.close();
            } else {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
            }
          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      } else {
        this.evChargingSolService.createEVChargingProvider(params).subscribe(
          (resp: any) => {
            this.snackBar.open(MessageConstant.menuMasterCreate, 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      }
      this.dialogRef.close(params);
    } else {
      this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 5000 });
    }
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
    this.form.get('name').setValue(params.data.name);
    this.form.get('role').setValue(this.params.data.role);
    this.form.get('offering').setValue(params.data.offerings);
    this.form.get('project').setValue(params.data.projects);
    this.form.get('remarks').setValue(params.data.remarks);
    this.status = this.params.data.status == 'Y' ? true : false;
  }

  updateStatus(event) {
    this.status = event.checked;
    this.active.checked = this.status;
  }





}










