import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})

export class EVChargingSolutionService {
    apiUrl2 = `${environment.API_URL2}/charger`;
    constructor(private http: HttpClient) { }

    getEvChargingSolutionProvider() {
        return this.http.get(`${this.apiUrl2}/getAllEVChargingProviderList`);
    }
    createEVChargingProvider(params){
        return this.http.post(`${this.apiUrl2}/CreateEVChargingProvider`,params);
    }
    updateEVChargingProvider(params){
        return this.http.post(`${this.apiUrl2}/updateEVChargingProvider`,params);
    }
    deleteEVChargingProvider(id,login_id){
        return this.http.delete(`${this.apiUrl2}/deleteEVChargingProvider/${id}/${login_id}`);
    }

    

   
    



}