
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EVChargingSolutionCrudComponent } from './EV-charging-solution-crud/EV-charging-solution-crud.component';
import { TABS_DEMO_ROUTES } from './EV-charging-solution-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : EVChargingSolutionCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class EVChargingSolutionRoutingModule{}