
import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import * as XLSX from 'xlsx';
import { EVChargingSolutionService } from 'app/shared/services/ev-charging-solution/ev-charging-solution.service';
import { ResizeEvent } from 'angular-resizable-element';


export class EVChargingSolution {
  id: number;
  name: string;
  role: string;
  offerings: string;
  projects: string;
  remarks: string;
  status: string;
  statusString: string;

}
export class resizeColumnSetting {
  coulmnName: string;
  csswidth: number;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})


export class ListComponent implements OnInit {

  filterEntity: EVChargingSolution;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['name', 'role', 'offerings', 'projects', 'remarks', 'status', 'action'];
  head = [['Name', 'Role', 'Offering', 'Project', 'Remarks', 'Status']]
  dataSource: MatTableDataSource<EVChargingSolution>;
  evChargingData: EVChargingSolution[];
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  dialogRef: any;
  nameSearch = false;
  roleSearch = false;
  offeringsSearch = false;
  projectsSearch = false;
  remarksSearch = false;
  statusSearch = false;
  exportedData: any[];
  localStorage: any;
  resizeColumnSettings: resizeColumnSetting[];
  //messageConstant = MessageConstant;
  constructor(private evChargingSolService: EVChargingSolutionService, public dialog: MatDialog,
    private snackBar: MatSnackBar, private cdr: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    
    this.defaultColumnSize();
    this.getEvChargingSolution();
    this.filterEntity = new EVChargingSolution();
    this.filterType = MatTableFilter.ANYWHERE;

  }

  defaultColumnSize(){
    this.resizeColumnSettings = [];
    for(var i =0; i < this.displayedColumns.length;i++){
        var columnSetting= new resizeColumnSetting();
        columnSetting.coulmnName = this.displayedColumns[i];
        columnSetting.csswidth = 0;
        this.resizeColumnSettings.push(columnSetting);
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getEvChargingSolution() {
    this.evChargingSolService.getEvChargingSolutionProvider().subscribe(
      (resp: any) => {
        if (resp != null) {
          this.evChargingData = resp.data;
          for (var i = 0; i < this.evChargingData.length; i++) {
            if (this.evChargingData[i].status == 'N') {
              this.evChargingData[i].statusString = 'Inactive';
            } else {
              this.evChargingData[i].statusString = 'Active';
            }
            if(this.evChargingData[i].name == null ){
              this.evChargingData[i].name = '';
            }
            if(this.evChargingData[i].role == null ){
              this.evChargingData[i].role = '';
            }
            if(this.evChargingData[i].offerings == null ){
              this.evChargingData[i].offerings = '';
            }
            if(this.evChargingData[i].projects == null ){
              this.evChargingData[i].projects = '';
            }
          }
          this.dataSource = new MatTableDataSource(this.evChargingData);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }

      },
      (err) => { }
    );
  }

  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });

    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
            this.evChargingSolService.deleteEVChargingProvider(id,this.localStorage.userId).subscribe(
            (resp: any) => {
              if(resp.status == true){
                this.getEvChargingSolution();
                this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 2000 });
                this.cdr.detectChanges();
              }else{
                this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 2000 });
              }
            }, (err) => {

          }
        )
      }
      this.confirmDialogRef = null;
    });

  }

  viewDialog(action, data): void {
    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1400px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getEvChargingSolution();
    });
  }
  openDialog(action, data): void {
    // this.snackBar.open('Unable to proceed, In prgress','Dismiss');
    // return false;
    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '1400px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getEvChargingSolution();
      this.cdr.detectChanges();
    });
  }

  changeVisibility(visibleOption,ev) {

    ev.stopPropagation();
    if (visibleOption == 'nameSearch') {
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = '';
    }
    if (visibleOption == 'roleSearch') {
      this.roleSearch = !this.roleSearch;
      this.filterEntity.role = '';
    }
    if (visibleOption == 'offeringsSearch') {
      this.offeringsSearch = !this.offeringsSearch;
      this.filterEntity.offerings = '';
    }
    if (visibleOption == 'projectsSearch') {
      this.projectsSearch = !this.projectsSearch;
      this.filterEntity.projects = '';
    }
    if (visibleOption == 'remarksSearch') {
      this.remarksSearch = !this.remarksSearch;
      this.filterEntity.remarks = '';
    }

    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }

  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var evCharging = this.dataSource.filteredData[i];
      var data = {
        'Name': evCharging.name, 'Role': evCharging.role, 'Offering': evCharging.offerings,
        'Project': evCharging.projects, 'Remarks ': evCharging.remarks, 'Status': evCharging.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
    XLSX.writeFile(workBook, 'EVChargingSolutionProvider.xlsx');
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var evCharging = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = [evCharging.name, evCharging.role, evCharging.offerings, evCharging.projects, evCharging.remarks,
      evCharging.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('EV-Charging Solution Provider Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not For Public Use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')

    // below line for Download PDF document  
    doc.save('EVChargingSolutionProvider report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example
    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right");
    return doc;

  }

  onResizeEnd(event: ResizeEvent, displayedColumns): void {
		if (event.edges.right) {
			const cssValue = event.rectangle.width + 'px';
			const columnElts = document.getElementsByClassName('mat-column-' + displayedColumns);
			for (let i = 0; i < columnElts.length; i++) {
				const currentEl = columnElts[i] as HTMLDivElement;
        currentEl.style.width = cssValue;
        this.cdr.detectChanges();
      }
      
      for(var i =0; i < this.resizeColumnSettings.length;i++){
        var columnSetting  = this.resizeColumnSettings[i];
         if(columnSetting.coulmnName ==  displayedColumns){
          columnSetting.csswidth = event.rectangle.width;
         }
        
    }
		}
  }
  changeColumnSize(){
    
    for(var i =0; i < this.resizeColumnSettings.length;i++){
        var columnSetting  = this.resizeColumnSettings[i];
       
       if(columnSetting.csswidth > 0){
        const columnElts = document.getElementsByClassName('mat-column-' + columnSetting.coulmnName);
        for (let i = 0; i < columnElts.length; i++) {
          const currentEl = columnElts[i] as HTMLDivElement;
          currentEl.style.width = columnSetting.csswidth +'px';
          this.cdr.detectChanges();
        }
       }
        
    }
  }

  responseClick(requestString){
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = JSON.stringify(requestString);
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
  }



}

